* {
    background-color: #dde2ff;
}

.question-page {
    margin: 5% 0 0 0;
    text-align: center;
    font-size:x-large;
    color: purple;
}

.question-content {
    text-align: center;
}

.question-content h2 {
    font-size:x-large;
    color: purple;
}

.question-content button {
  background-color: #fff;
  border-radius: 14px;
  margin: 0 3%;
  border-style: none;
  color: #3c4043;
  cursor: pointer;
  font-size: large;
  padding:15px 24px;
  text-align: center;
}

.question-content button:hover {
    text-transform: none;  
    box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px,rgba(0, 0, 0, .14) 0 6px 10px 0,rgba(0, 0, 0, .12) 0 1px 18px 0;
    box-sizing: border-box;
    transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1),opacity 1ms linear 1ms,transform 1ms cubic-bezier(0, 0, .2, 1);
    background: #F6F9FE;
    color: #174ea6;
}